.category {
  position: absolute;
  top: 80px;
  left: 50px;
  font-size: 3rem;
  font-weight: 600;
  z-index: 2;
  display: flex;
  align-items: center;

  .title-cate {
    margin-right: 10px;
  }

  .button-dropdown {
    font-size: 12px;
    position: relative;
    z-index: 1;
    .dropdown-toggle {
      display: flex;
      align-items: center;
      min-width: 4em;
      font-weight: 600;
      position: relative;
      font-size: 1.5em;
      background-color: rgb(36, 36, 36);
      padding: 0.5em 1em;
      color: #fff;
      cursor: pointer;
      border: 0.1em solid rgb(77, 77, 77);
      border-radius: 0.2em;

      &::after {
        content: "";
        border-left: 0.3em solid transparent;
        border-right: 0.3em solid transparent;
        border-top: 0.4em solid rgb(255, 255, 255);
        transition: transform 0.2s linear 0s;
        margin-left: 2em;
        transform: none;
      }
    }

    .rotate {
      &::after {
        transform: rotate(180deg) !important;
      }

      background-color: rgba(255, 255, 255, 0.1);
    }

    .dropdown-menu {
      width: 15em;
      position: absolute;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      right: 0px;
      font-weight: 400;
      cursor: pointer;
      color: #fff;
      list-style-type: none;
      border: 0.1em solid rgb(77, 77, 77);
      margin-top: 0.1em;
      font-size: 1.5em;
      z-index: 1;
      background-color: rgb(36, 36, 36);
      white-space: nowrap;
      column-gap: 1.8em;
      max-height: max(10em, 347.475px - 2rem);
      overflow: auto;
      padding: 1rem 0px;

      .dropdown-item {
        min-width: 4em;
        padding: 0.5em 0.8em;
        display: block;
        break-inside: avoid;

        &:hover {
          text-decoration: underline;
        }

        .dropdown-item-length {
          font-size: 14px;
          font-weight: 400;
        }
      }
      .breaker {
        margin: 0.5rem 0.8em;
        border: 1px solid rgb(77, 77, 77);
        background-color: rgb(77, 77, 77);
      }
    }
  }
}
